import Vue from 'vue'
import VueRouter from 'vue-router';
import Simulation from '../views/Simulation.vue';
import Index from '../views/Index.vue';

Vue.use(VueRouter)

const routes = [
  {
    path: '/',
    name: 'Simulation',
    component: Simulation
  },
  {
    path: '/index',
    name: 'Index',
    component: Index
  },
]

const router = new VueRouter({
  mode: 'history',
  routes: routes
})

export default router
